import React from "react";
import { Link } from "react-router-dom";

import { reloadPage } from '../function/page';
import {useEffect} from 'react';
  
const NotFound = () => {

  useEffect(() => {

    reloadPage();

  }, [])

  return (
    <div className="container" style={{zIndex: "99", backgroundColor: "black"}}>
        <div id='page-banner'>
            <div className='overlay-img'
                style={{backgroundImage: 'url(https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/627bc589-b21e-47b2-8cf2-7617185ee1ac/d4n4zuk-f6474775-f789-459c-833e-89ffd068b654.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..9XxbT2tBslr_SHGEAA4u_5QCwhppgUj6K-BiJsHo4ms)'}}
            ></div>
        </div>
        <div className='content page-title'>
            <h2>NOT FOUND</h2>
            <h1>404</h1>
            <p>Trang ny khng tn ti hoc cha ra mt</p>
            <Link to='/'><u>V TRANG CH</u></Link>
        </div>
    </div>
  );
};
  
export default NotFound;