import { useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Swiper, SwiperSlide } from "swiper/react";

import List from './../components/list'
import Banner from './../components/banner'

import "swiper/css";
import "swiper/css/pagination";
import './../css/home-layout.css';

import { Mousewheel, Pagination } from "swiper";

import { reloadPage } from '../function/page'
import data from '../data/test-data.json';

const img = {url: 'https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/627bc589-b21e-47b2-8cf2-7617185ee1ac/d4n4zuk-f6474775-f789-459c-833e-89ffd068b654.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..9XxbT2tBslr_SHGEAA4u_5QCwhppgUj6K-BiJsHo4ms',
                alt: 'viresty'}

const Home = () => {

    const homeMenu = [
        {
            name: "CHI NGAY!!!"
        }, {
            name: "LUT CHI"
        }, {
            name: ""
        }, {
            name: "CHC NGHIP"
        }, {
            name: "CHI NGAY!!!"
        }, {
            name: "NG K"
        }
    ];    
    
    useEffect(() => {
        
        document.getElementById('footer-container').classList.add("closed");
        reloadPage();
        
        return () => {
            document.getElementById('footer-container').classList.remove("closed");
            document.getElementById('footer-container').classList.remove("hidden");
        };
    
    }, [])

    return (
        <div className='container home-layout'>
            <div id='page-banner'>
                <div className='overlay-img'></div>
            </div>
            <div className='container' id="body-container">
                <Swiper
                    direction={"vertical"}
                    preventClicks={false}
                    slidesPerView={1}
                    mousewheel={{
                        enable: false,
                        releaseOnEdges: true
                    }}
                    pagination={{
                        clickable: true,
                        renderBullet: function (index, className) {
                            return '<span class="' + className + '">' +
                                        '<p>' + homeMenu[index].name + '</p>' +
                                    
                                    '</span>';
                        }
                    }}
                    onSlideChange={(swiper) => {
                        if (swiper.isEnd) {
                            document.getElementById('footer-container').classList.remove("hidden");
                            document.getElementById("footer-container").scrollIntoView();
                        }
                        else {
                            document.getElementById('footer-container').classList.add("hidden");
                            document.getElementById("body-container").scrollIntoView();
                        }
                    }}
                    modules={[Mousewheel, Pagination]}
                    className="homeSwiper"
                >
                    <SwiperSlide>
                    <div className='content page-title FadeInAnimation'>
                        <h2>VUA HM NGC</h2>
                        <h1 className='textshadowAnimation'>VIRESTY</h1>
                        <p>Game th bi chin u, sinh tn v khm ph hm ngc</p>
                        <a className='big-button' href='#/play'>CHI NGAY</a>
                    </div>
                    </SwiperSlide>
                    <SwiperSlide>
                        <div className="content fade-in">
                            <h1 className="content-title">Khm ph hm ngc v tn.</h1>
                            <p className="content-detail">Hy cng nh bi v s chng qui vt hng mnh, truy tm cc vt phm qu him v nhng th thch kh khn khng th lng trc.</p>
                            <img className='open-img content-img content-main' src={img.url}
                                alt="viresty"></img>
                            <Link to={'/rule'} className='content-link'>LUT CHI <i className="fa fa-chevron-right" aria-hidden="true"></i></Link>
                        </div>
                    </SwiperSlide>
                    <SwiperSlide>
                    <div className="content fade-in">
                        <h1 className="content-title">Lp k hoch, chun b hnh trang</h1>
                        <p className="content-detail">Tn dng kho bu tm c  tng chin lc hoc mua bn cc vt phm  chun b cho hnh trnh sp ti.</p>
                        <img className='open-img content-img content-main' src=''
                            alt="viresty"></img>
                        <Link to={'/library'} className='content-link'>XEM THM <i className="fa fa-chevron-right" aria-hidden="true"></i></Link>
                    </div>
                    </SwiperSlide>
                    <SwiperSlide>
                    <div className='content fade-in'>
                        <h1 className='content-title'>Chc nhip a dng</h1>
                        <p className="content-detail">Mi chc nghip u c im mnh, ch s c bn, kh nng v cc k nng tt st mnh m ring bit.</p>
                        <p className='content-notice'>Nhp chn  xem chi tit.</p>
                        <div className='content-main'>
                            <Banner ListItems={data[0]}/>
                        </div>
                        <Link to={'/library'} className='content-link'>XEM THM <i className="fa fa-chevron-right" aria-hidden="true"></i></Link>
                    </div>
                    </SwiperSlide>
                    <SwiperSlide>
                    <div className='content fade-in'>
                        <h1 className='content-title'>Chin thut linh ng</h1>
                        <p className="content-detail">Hng trm th bi trang b v php thut khin mi chuyn chinh pht u tr nn mi m v th v.</p>
                        <div className='content-main'>
                            <List ListItems={data[0]} shortlist={true} cardPreview={true} link="/library/" />
                        </div>
                    </div>
                    </SwiperSlide>
                    <SwiperSlide>
                    <div className="content">
                        <div className='content-item'>
                            <h1 className='content-title'>Cn chn ch g na, hy tr thnh th sn kho bu v khm ph hm ngc VIRESTY v i.</h1>
                            <a className='big-button' href='#/register'>NG K NGAY</a>
                            <div className='overlay-img'></div>
                        </div>
                    </div>
                    </SwiperSlide>
                </Swiper>
            </div>
        </div>
  );
}

export default Home;